*, *:before, *:after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.background {
    min-width: 100%;
    background-color: aquamarine;
    border: 5px ridge burlywood;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.title {
    font-size: 55px;
    font-family: 'Times New Roman', Times, serif;
    color: blue;
    margin-top: 50px;
    margin-bottom: 25px;
}

.firstComplect {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.firstAudios, .separateAudio, .secondAudios {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.separateAudio {
    margin-top: 250px;
    margin-left: 200px;
}

.firstAudios, .secondAudios {
    margin-left: 50px;
    margin-top: 25px;
    margin-bottom: 25px;
}

.audiosFirst, .audioSeparate, .audiosSecond {
    width: 400px;
}

h1 {
    font-size: 17px;
}

.middleLine {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 25px;
    margin-bottom: 25px;
    margin-left: 50px;
}

.big {
    width: 700px;
}

.secondComplect {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}